
body {
  margin: 0;
  font-family: 'Open Sans';
  background: #B6CDBA
}

.navbar {
  background: #223239;
  height: 48px;
  text-align: center;
  color: white;
  padding-top: 12px;
  font-size: 30px;
  letter-spacing: 2px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}

.feed {
  max-width: 1000px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 92px auto 0;
}

.recipe {
  width: 31%;
  margin-bottom: 48px;
  text-decoration: none;
  background: #DFE6CC;
  padding: 4px;

  &__image {
    max-width: 100%;
  }

  &__source {
    font-size: 14px;
    color: #131922;
  }

  &__title {
    font-size: 21px;
    color: #000000;
    word-break: break-word;
  }
}


